<template>
<!--  -->
  <div class="loctionnav" id="nav">
    <div class="nav-loction">
      <ul>
          <span><strong>|</strong></span>
          <li v-for="(list, index) in tabs" :key="index">
            <router-link class="linkTo" :to="list.to">  <!-- @mouseenter.native="childShow(index)-->
              {{list.text}}
            </router-link>
          </li>
          <li  v-for="(list,index) in nav" :key="index">
            <router-link class="linkTo" :to="'/Product/' + list.name">  <!-- @mouseenter.native="childShow(index)-->
              {{ list.name }}
            </router-link>
          </li>
      </ul>
    </div>
    <gate v-if="childshow" @mouseleave.native="childhidden"></gate>
  </div>
</template>

<script>
import axios from 'axios'
import gate from '@/components/navigate' //div
export default {
   components: {
      'gate': gate
  },
  data() {
    return {
      childshow: false,
      tabs: [
        { text: 'NEW IN', to:'/NEWIN' },
        { text: 'FLASH SAIE', to:'/Product/FLASHSAIE' },
        { text: 'DESIGNERS',to:'/Designers'},
        { text: 'MEMBER',to:'/Product/MEMBER'},
        { text: 'DRESSES',to:'/Product/DRESSES'},
        { text: 'TOPS',to:'/Product/TOPS'}
      ],
      nav: []
    }
  },
  created() {
    window.addEventListener('scroll',this.menuFixed) //
  },
  methods: {
      menuFixed: function (nav){ //
      var obj = document.getElementById('nav');
      var scrolled = window.pageYOffset < 125;
      if (scrolled) {
        obj.style.position = 'static'
      } else {
          obj.style.position = 'fixed'
          obj.style.top = 0
      }
    },
    /*childShow: function (index) {
      if(index !== 0) {
        this.childshow = true
      }
  },*/
    childhidden: function () {
      this.childshow = false
    } 
  },
  mounted: function () {
    var self = this
    axios.get('/category?page=all')
      .then(function (res) {
        self.nav = res.data.object_list
        console.log(res)
      })
      .catch(function () {
        console.log(1)
      })
  }
}
</script>
<style scoped>
/*  */
.loctionnav {
  padding-top: 10px;
  position: relative;
  width: 100%;
  height: 30px;
  z-index: 1000;
  line-height: 12px;
  background: #fafafa;
  border-bottom: 1px solid #e5e5e5;
}
/* div */
.nav-loction {
  width: 1080px;
  height: 30px;
  margin: 0 auto;
  vertical-align: middle;
}
.nav-loction ul {
  float: left;
  margin-left: -8px;
}
.nav-loction li {
  height: 20px;
  font-size: 14px;
  float: left;
  margin-right: 15px;
}
/* 16li */

/* | */
.nav-loction strong {
  float: left;
  position: relative;
  left: 266px;
  top: -2px;
  color: #b7b7b7;
}
.linkTo:hover {
  color: #E54C87
}
.linkTo:focus {
  color: #E54C87
}
</style>


